.homeHeader::after {
  display: block;
  height: 50px;
  content: "";
}

.homeHeader__wrapper {
  position: fixed;
  width: 100%;
  height: 50px;
  line-height: 50px;
  background-color: #f63;
  color: #fff;
  padding: 0 10px;
  text-align: center;
  display: -webkit-box;
  z-index: 305;
  box-sizing: border-box;
}

.homeHeader__city {
  position: relative;
  padding-right: 17px;
  color: #fff;
  max-width: 60px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  text-decoration: none;
}

